<template>
  <transition :name="transitionName">
    <div class="mobile-category">
      <div class="category-wrapper" :style="categoryWrapperStyle">
        <nav-bar :dataList="tabs" v-model="currentTabIndex"></nav-bar>
        <goods-list :style="{flex:1}" :dataList="tabs" v-model="currentTabIndex"></goods-list>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
import { tabs } from "../data.js";

let labels = tabs.map(tab => {
  return tab.title;
});

export default {
  name: "mobileCategory",
  mounted() {
    this.categoryWrapperStyle.width = `${this.appWidth}px`;
    this.categoryWrapperStyle.height = `${this.appHeight -
      this.footerHeight}px`;
  },
  props: {},
  data() {
    return {
      transitionName: "",
      categoryWrapperStyle: {
        width: "300px",
        height: "600px"
      },
      tabs,
      labels,
      currentTabIndex: 0
    };
  },
  computed: {
    ...mapGetters(["appWidth", "appHeight"]),
    ...mapGetters("app.mobile", ["footerHeight"])
  },
  methods: {
    ...mapMutations(["setThemeColor"])
  },
  beforeRouteEnter: (to, from, next) => {
    next(vm => {
      vm.setThemeColor("#f2f2f2");
    });
  },
  components: {
    NavBar: () =>
      import(/* webpackChunkName: "nav-bar" */ "./components/NavBar"),
    GoodsList: () =>
      import(/* webpackChunkName: "goods-list" */ "./components/GoodsList")
  }
};
</script>

<style lang="scss">
.mobile-category {
  background-color: white;
  .category-wrapper {
    display: flex;
    align-items: stretch;
  }
}
</style>
